.form {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 30px auto;
  width: 50%;
}

.input {
  margin: 0 30px !important;
  width: 50%;
}

.btn {
  background: transparent;
  color: white;
  border: 1px solid white;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  font-weight: 900;
}
.btn:hover {
  background: white !important;
  color: #177E89;
}

@media (max-width: 524px) {
  .form {
    flex-direction: column;
  }
}

